:root {
  --color-bg: #15151A;
  --color-bg-secondary: #171720;
  --color-primary: rgba(100,50,50,0.8);

}

html {
  background-color: var(--color-bg-primary);
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 2rem;
  background-color: var(--color-bg);

  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 50%;
  border-radius: 2px;
  box-shadow: 0px -2px #3c4fe0;

}

.footer-title {
  text-align: right;
  font-size: 8px;
  color: #bfbfbf;
  text-transform: capitalize;


}

.footer-info {
  font-size: 8px;
  color: #8c8c8c;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 10px;
}

.utc {
  vertical-align: bottom;
  font-size: 6px;
  color: #A0A0A0;

}

